n\n.as-login-body ul li {\n  display: flex;\n  margin-bottom: 15px;\n  align-items: center;\n}\n\n.as-login-body ul li * {\n  fill: #0CCA4A;\n}\n\n.as-login-body ul li.coming-soon {\n  color: #6C5AF6;\n  margin-top: 35px;\n}\n\n.as-login-body ul li.disabled,\n.as-login-body ul li.disabled * {\n  color: #ABB1BF;\n  fill: #ABB1BF;\n}\n\n.as-create-account-button {\n  margin-bottom: 20px;\n  margin-top: 20px;\n  border-radius: 4px;\n  padding: 10px 16px;\n  background: #0CCA4A;\n  border: none;\n  color: #06262D;\n  cursor: pointer;\n  font-size: 16px;\n  font-weight: 700;\n  line-height: 24px;\n}\n\n.as-create-account-button:hover {\n  background: #4FAB4F;\n}\n\n.as-login-sign-in-text {\n  font-size: 14px;\n  font-weight: 400;\n  line-height: 17px;\n}\n\n.as-login-footer {\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 20px;\n  display: flex;\n  margin-bottom: 60px;\n}\n\n.as-login-footer svg,\n.as-login-body svg {\n  width: 17px;\n  height: 17px;\n  margin-right: 10px;\n}\n\n.as-login-footer svg {\n  width: 20px;\n  height: 20px;\n}\n.as-login-icon {\n  min-width: 30px;\n  max-width: 30px;\n  color: #31a824;\n}\n\n.as-title-text {\n  font-size: 24px;\n  font-weight: 700;\n  margin-left: 10px;\n  font-family: \"Inter\", sans-serif;\n}\n\n.as-subtitle-text {\n  font-size: 16px;\n  font-weight: 400;\n  line-height: 24px;\n}\n\n.as-sub-title-container {\n  display: flex;\n  gap: 5px;\n  width: 80%;\n  color: #2c303a;\n  background: #43b0491f;\n  padding: 10px 12px;\n  border-radius: 8px;\n  margin-top: 25px;\n}\n\n.as-home-screen-header {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  margin-bottom: 10px;\n}\n\n.as-home-screen-container {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  padding-left: 20px;\n  padding-right: 20px;\n  background: white;\n}\n\n.as-home-screen-icon {\n  width: 80px;\n  color: #31a824;\n}\n\n.as-sub-title-text {\n  font-size: 14px;\n  line-height: 20px;\n  font-weight: 400;\n  text-align: center;\n}\n\n.as-home-screen-body {\n  font-size: 13px;\n}\n\n.as-home-screen-section {\n  display: flex;\n  align-items: center;\n  font-size: 13px;\n  font-weight: 590;\n  letter-spacing: 0.02em;\n  margin-top: 20px;\n  margin-bottom: 10px;\n}\n\n.as-home-screen-df-icon-container {\n  width: 26px;\n  height: 26px;\n  background: #e9e6fe;\n  border-radius: 100px;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  margin-right: 10px;\n}\n\n.as-home-screen-df-icon {\n  width: 12px;\n  height: 12px;\n  color: #6c5af6;\n}\n\n.as-home-screen-code-icon-container {\n  width: 26px;\n  height: 26px;\n  background: rgba(0, 178, 255, 0.1);\n  border-radius: 100px;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  margin-right: 10px;\n}\n\n.as-home-screen-code-icon {\n  width: 13px;\n  color: #00b2ff;\n}\n\n.as-home-screen-chat-starter {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  border: 1px solid #e6e6e6;\n  margin-bottom: 3px;\n  border-radius: 8px;\n  padding: 9px 12px 9px 12px;\n  cursor: pointer;\n}\n\n.as-home-screen-chat-starter:hover {\n  background: rgba(33, 33, 33, 0.05);\n}\n\n.as-home-screen-starter-icon {\n  width: 8px;\n  height: 12px;\n  color: rgba(33, 33, 33, 0.5);\n}\n\n.as-version-text {\n  color: #bdbdbd;\n  font-size: 11px;\n  margin-top: 4px;\n  margin-left: 10px;\n}\n\n.as-graph-selector-container {\n  display: flex;\n  flex-direction: column;\n  font-size: 13px;\n  padding-left: 20px;\n  padding-right: 20px;\n  margin-bottom: 20px;\n}\n\n.as-graph-selector-message-container {\n  display: flex;\n  align-items: center;\n  height: 50px;\n  margin-top: 5px;\n  margin-bottom: 8px;\n}\n\n.as-code-share-warning-message,\n.as-dataframe-select-warning-message {\n  margin-bottom: 20px;\n  color: #757575;\n  background: #fcfccc;\n  padding: 10px;\n  font-size: 11px;\n  border-radius: 4px;\n  border: 1px solid #ebebbe;\n  display: flex;\n}\n\n.as-warning-icon-wrapper {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  padding: 0 12px 0 3px;\n}\n\n.as-warning-icon {\n  width: 23px;\n  color: #ffba0a;\n}\n\n.as-dataframe-select {\n  margin: 0;\n  box-sizing: border-box;\n  appearance: none;\n  background-color: white;\n  border: 1px solid #e0e0e0;\n  border-radius: 4px;\n  display: inline-block;\n  font: inherit;\n  font-size: 13px;\n  height: 40px;\n  line-height: 1em;\n  padding: 0.5em 3.5em 0.5em 1em;\n  background-image: linear-gradient(45deg, transparent 50%, gray 50%),\n    linear-gradient(135deg, gray 50%, transparent 50%),\n    linear-gradient(to right, #ccc, #ccc);\n  background-position: calc(100% - 20px) calc(1em + 4px),\n    calc(100% - 15px) calc(1em + 4px), calc(100% - 2.5em) 9px;\n  background-size: 5px 5px, 5px 5px, 1px 20px;\n  background-repeat: no-repeat;\n}\n\nselect:invalid {\n  color: gray;\n}\n\n.as-get-code-button {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0.35em 1.2em;\n  border: 0.1em solid #ffffff;\n  margin-top: 16px;\n  height: 40px;\n  border-radius: 4px;\n  box-sizing: border-box;\n  text-decoration: none;\n  font-size: 13px;\n  font-weight: 300;\n  color: #06262d;\n  text-align: center;\n  transition: all 0.5s;\n  background: #0cca4a;\n}\n\n.as-get-code-button.disabled {\n  background: #bdbdbd;\n}\n\n.as-get-code-button:hover {\n  background-color: #4fab4f;\n  cursor: pointer;\n}\n\n.as-get-code-button:hover.disabled {\n  cursor: not-allowed;\n  background: #bdbdbd;\n}\n\n.as-get-code-button-icon {\n  width: 16px;\n  height: 16px;\n  margin-right: 10px;\n}\n\n.as-no-dataframe-container {\n  display: flex;\n  flex-direction: column;\n  background: #f7f7f7;\n  border-radius: 4px;\n  padding: 20px 10px;\n}\n\n.as-no-dataframe-heading {\n  font-size: 14px;\n  font-weight: lighter;\n  margin-bottom: 20px;\n}\n\n.as-no-dataframe-text {\n  font-size: 12px;\n  margin-bottom: 10px;\n}\n\n.as-no-dataframe-link {\n  display: inline;\n  color: blue;\n  text-decoration: underline;\n}\n\n.as-no-dataframe-link:hover {\n  color: darkblue;\n  text-decoration: underline;\n}\n\n/*Main Chat Screen*/\n\n.as-chat-header-container {\n  display: flex;\n  min-height: 50px;\n  height: 50px;\n  align-items: center;\n  font-family: \"Inter\", sans-serif;\n}\n\n.as-chat-header-title-container {\n  flex: 1;\n  display: flex;\n  min-height: 50px;\n  height: 50px;\n  align-items: center;\n  border-bottom: 1px solid rgba(85, 85, 85, 0.05);\n}\n\n.as-chat-header-settings-container {\n  display: flex;\n  justify-content: flex-end;\n  min-height: 50px;\n  height: 50px;\n  align-items: center;\n  width: 50px;\n}\n\n.as-chat-header-text-icon {\n  display: flex;\n  justify-content: center;\n}\n\n.as-chat-header-main-icon {\n  width: 20px;\n  height: 15px;\n  color: #31a824;\n  padding-left: 15px;\n  margin-right: 7px;\n}\n\n.as-chat-header-main-text {\n  color: #2c303a;\n  font-style: normal;\n  font-weight: 700;\n  font-size: 16px;\n  line-height: 24px;\n}\n\n.as-chat-header-version-text {\n  color: #bdbdbd;\n  font-size: 11px;\n  padding-left: 10px;\n  padding-right: 10px;\n  margin-top: 2px;\n}\n\n.as-chat-header-pro-tier-text {\n  color: white;\n  font-size: 11px;\n  padding-left: 10px;\n  padding-right: 10px;\n  margin-top: 2px;\n  background: #31a824;\n  border-radius: 4px;\n  padding: 3px 6px 1px;\n}\n\n.as-chat-main-section {\n  overflow-y: scroll;\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n}\n\n.as-chat-message-container {\n  display: flex;\n  flex-direction: column;\n}\n\n.as-user-chat-message-buttons-container {\n  display: flex;\n  flex-direction: column;\n  background: #e9e6fe;\n}\n\n.as-user-chat-message-container {\n  display: flex;\n  flex-direction: column;\n  align-items: start;\n  padding: 10px 20px 5px;\n  min-height: 50px;\n}\n\n.as-user-chat-message-buttons-container:first-child\n  .as-user-chat-message-container,\n.as-user-chat-message-buttons-container:nth-child(2)\n  .as-user-chat-message-container {\n  flex-direction: row;\n  align-items: center;\n  padding: 5px 20px;\n}\n\n.as-user-chat-message-icon-container {\n  background: #6c5af6;\n  padding: 6px 7px;\n  margin-right: 10px;\n  border-radius: 4px;\n}\n\n.as-user-chat-message-buttons-container:not(:first-child):not(:nth-child(2))\n  .as-user-chat-message-icon-container {\n  margin-bottom: 5px;\n}\n\n.as-user-chat-message-icon {\n  width: 14px;\n  height: 16px;\n  color: white;\n}\n\n.as-user-chat-message-text {\n  font-size: 13px;\n  font-weight: lighter;\n  font-style: normal;\n  word-break: break-word;\n  padding: 6px 0;\n  white-space: pre-wrap;\n  margin-left: 1px;\n}\n\n.as-user-chat-message-text-bold {\n  font-size: 13px;\n  font-weight: bold;\n  font-style: normal;\n  margin-left: 5px;\n}\n\n.as-code-container {\n}\n\n.as-code-message-container {\n  display: flex;\n  align-items: center;\n  height: 50px;\n  padding-left: 20px;\n  padding-right: 20px;\n}\n\n.as-code-message-icon-container {\n  background: #4fab4f;\n  border-radius: 4px;\n  margin-right: 10px;\n  padding: 8px 4px 5px 4px;\n}\n\n.as-code-message-icon {\n  width: 20px;\n  height: 16px;\n  color: white;\n}\n\n.as-code-message-text {\n  font-size: 13px;\n  font-weight: lighter;\n  font-style: normal;\n}\n\n.as-code-loading {\n  font-size: 13px;\n}\n\n.as-code-loading:after {\n  overflow: hidden;\n  display: inline-block;\n  vertical-align: bottom;\n  -webkit-animation: ellipsis steps(4, end) 1200ms infinite;\n  animation: ellipsis steps(4, end) 1200ms infinite;\n  content: \"\\2026\"; /* ascii code for the ellipsis character */\n  width: 0;\n}\n\n.as-export-chat-container {\n  display: flex;\n  align-items: center;\n  padding: 0 20px;\n  min-height: 50px;\n  background: linear-gradient(white, white, white, #fdfdfd, #fafafa, #f8f8f8);\n  justify-content: center;\n  color: #ccc;\n  gap: 10px;\n  font-size: 12px;\n  cursor: pointer;\n  margin-top: auto;\n}\n\n.as-export-chat-container:hover {\n  color: #6c5af6;\n  text-shadow: 1px 1px 0 white;\n}\n\n.as-export-chat-container svg {\n  height: 12px;\n}\n\n@keyframes ellipsis {\n  to {\n    width: 1.25em;\n  }\n}\n\n@-webkit-keyframes ellipsis {\n  to {\n    width: 1.25em;\n  }\n}\n\n.as-code-generate-container {\n  width: 100%;\n  display: flex;\n  justify-content: space-between;\n}\n\n.as-code-generate-refresh {\n  color: rgb(56, 116, 203);\n  cursor: pointer;\n}\n\n.as-code-generate-refresh-disabled {\n  color: rgb(56, 116, 203, 0.5);\n  cursor: not-allowed;\n}\n\n.as-code-block {\n  padding-left: 20px;\n  padding-right: 20px;\n}\n\n.as-text-block {\n  padding-left: 21px;\n  padding-right: 20px;\n  font-size: 13px;\n  line-height: 20px;\n  color: #20262f;\n  white-space: pre-wrap;\n  word-break: break-word;\n}\n\n.as-code-error-text {\n  font-size: 13px;\n  font-weight: lighter;\n  font-style: normal;\n}\n\n.as-code-error-panel {\n  margin-left: 20px;\n  margin-right: 20px;\n  border-radius: 4px;\n  background: #fef1f1;\n  display: flex;\n  padding: 16px;\n}\n\n.as-code-error-icon {\n  width: 16px;\n  height: 16px;\n  min-width: 16px;\n  min-height: 16px;\n  color: #f44336;\n  margin-right: 8px;\n}\n\n.as-code-error-panel-text {\n  font-size: 13px;\n  font-weight: lighter;\n  font-style: normal;\n  color: #d60000;\n}\n\n.as-code-error-footer {\n  display: flex;\n  margin-left: 20px;\n  margin-top: 8px;\n}\n\n.as-code-retry-button {\n  display: flex;\n  border: none;\n  background: #3d3d3d;\n  justify-content: center;\n  align-items: center;\n  border-radius: 4px;\n  font-size: 13px;\n  font-weight: lighter;\n  font-style: normal;\n  padding: 8px;\n  margin-right: 8px;\n  color: white;\n  cursor: pointer;\n}\n\n.as-code-retry-icon,\n.as-code-copy-icon {\n  width: 16px;\n  height: 16px;\n  margin-right: 8px;\n}\n\n.as-code-error-token-limit .as-code-error-panel,\n.as-code-error-upgrade .as-code-error-panel {\n  background: #43b0491f;\n  margin-top: 20px;\n  margin-bottom: 20px;\n}\n\n.as-code-error-token-limit .as-code-error-panel-text a {\n  color: #6c5af6;\n  text-decoration: underline;\n}\n\n.as-code-error-token-limit .as-code-error-panel-text,\n.as-code-error-upgrade .as-code-error-panel-text {\n  color: #2c303a;\n  margin-left: 10px;\n  font-size: 13px;\n}\n\n.as-code-error-upgrade .as-code-error-icon {\n  min-width: 42px;\n  color: #31a824;\n  margin-right: 8px;\n}\n\n.as-code-error-token-limit .as-code-error-icon {\n  min-width: 42px;\n  color: #31a824;\n  margin-right: 4px;\n  margin-top: 2px;\n}\n\n.as-code-error-token-limit .as-code-error-token-limit-restart-button svg {\n  width: 12px;\n  margin-right: 4px;\n}\n\n.as-code-error-token-limit .as-code-retry-button,\n.as-code-error-upgrade .as-code-retry-button {\n  background: #4fab4f;\n  color: white;\n  font-size: 16px;\n  margin-right: 20px;\n  margin-bottom: 40px;\n}\n\n.as-code-error-token-limit .as-code-retry-button:hover,\n.as-code-error-upgrade .as-code-retry-button:hover {\n  background: #0cca4a;\n  color: #06262d;\n}\n\n.as-instruction-container {\n  display: flex;\n  height: 50px;\n  padding-left: 20px;\n  padding-top: 8px;\n  padding-right: 20px;\n  margin-bottom: 10px;\n}\n\n.as-instruction-input-form {\n  display: flex;\n  align-items: end;\n  width: 100%;\n  min-height: 36px;\n  background: #ffffff;\n  border: 1px solid #e0e0e0;\n  box-shadow: 0 0 8px rgba(0, 0, 0, 0.07);\n  border-radius: 8px;\n  padding: 7px 6px 6px 16px;\n  margin-bottom: 10px;\n  max-height: 215px;\n}\n\n.as-instruction-input-textarea {\n  border: none;\n  resize: none;\n  flex: 1;\n  font-family: Tahoma, sans-serif;\n  background: canvas;\n  font-size: 12px;\n  line-height: 16px;\n  min-height: 16px;\n  max-height: 200px;\n  margin-bottom: 8px;\n}\n\n.as-instruction-input-textarea:disabled {\n  cursor: not-allowed;\n}\n\n.as-instruction-input-textarea::placeholder:disabled {\n  cursor: not-allowed;\n  color: red;\n}\n.as-instruction-input-textarea:disabled::placeholder {\n  color: rgba(211, 211, 211, 0.5);\n}\n\n.as-instruction-input-textarea:focus {\n  border: none;\